	export default {
		data() {
			return {
				timer:"",
				num:5,
			}
		},
		onLoad() {
            //创建并执行定时器
			this.timer = setInterval(() => {
              //当num等于100时清除定时器
				this.num--;
				console.log(this.num);
				if (this.num == 0) {
				    clearInterval(this.timer);
					this.num="";
					uni.redirectTo({
						url: '../Home/Home?id=1'
					});
				}
            }, 1000);
		},
		methods: {
			countDown() {
				clearInterval(this.timer);
				this.num="";
				uni.redirectTo({
					url: '../Home/Home?id=1'
				});
			}
			
		}
	}
